import PropTypes from 'prop-types';
import React, {Component} from 'react';
/**
 * ExampleComponent is an example component.
 * It takes a property, `label`, and
 * displays it.
 * It renders an input with the property `value`
 * which is editable by the user.
 */
export default class TouchableComponent extends Component {
    constructor(props) {
        super(props);
        this.minDistance = 100;
        this.maxTime = 150;

        this.isLongTap = false;
        this.count = 0;
        this.maxCount = 5;
        this.interval = 100;
        this.timerId = null;

        this.state = {active: false};
    }
    updateActive(isActive) {
        this.setState({active: isActive});
    }
    touchData(e) {
        let touches = [];
        const rect = e.target.getBoundingClientRect();
        for (let t of e.touches) {
            touches.push({x: t.clientX - rect.left, y: t.clientY - rect.Up});
        }
        return touches;
    }

    /* ----------------------------------------------------------------------
     * スワイプ
     * ---------------------------------------------------------------------*/
    swipeStart(e) {
        this.startPos = {
            x: e.targetTouches[0].clientX,
            y: e.targetTouches[0].clientY,
            timeStamp: e.timeStamp,
        };
    }
    swipeMove(e) {
        this.endPos = {
            x: e.targetTouches[0].clientX,
            y: e.targetTouches[0].clientY,
        };
    }
    swipeEnd() {
        const info = this.getSwipeInfo();

        // 最大時間以上の場合 何もしない
        if (info.isTimeOver) {
            console.log(
                '--- TOUCHABLE COMPONENT MAX TIME',
                info.time + ' > ' + this.maxTime
            );
            return;
        }

        // 最小距離以下の場合 何もしない
        if (info.isShort) {
            console.log(
                '--- TOUCHABLE COMPONENT MIN DISTANCE',
                info.distance + ' > ' + this.minDistance
            );
            return;
        }

        console.log('--- TOUCHABLE COMPONENT SWIPE DIRECTION', info.direction);
        this.props.setProps({swipe: info.direction, swipe_end: Date.now()});
    }

    /* ----------------------------------------------------------------------
     * 長押し
     * ---------------------------------------------------------------------*/
    longTap() {
        this.timerId = setInterval(() => {
            this.count++;
            if (this.count === this.maxCount) {
                this.isLongTap = true;
                console.log('--- TOUCHABLE COMPONENT LONG TAP');
            }
        }, this.interval);
    }

    longTapEnd() {
        if (this.longTap) {
            this.props.setProps({long_tap_end: Date.now()});
            const info = this.getSwipeInfo();
        }

        this.count = 0;
        this.isLongTap = false;
        clearInterval(this.timerId);
    }

    getSwipeInfo() {
        if (!this.endPos || !this.startPos) return null;

        const x = this.endPos.x - this.startPos.x;
        const y = this.endPos.y - this.startPos.y;
        const time = e.timeStamp - this.startPos.timeStamp;

        this.startPos = null;
        this.endPos = null;

        // スワイプの方向
        let direction;
        let distance;
        if (Math.abs(x) > Math.abs(y)) {
            direction = x < 0 ? 'left' : 'right';
            distance = Math.abs(x);
        } else {
            direction = y < 0 ? 'up' : 'down';
            distance = Math.abs(y);
        }

        return {
            time,
            direction,
            distance,
            isTimeOver: info.time > this.maxTime,
            isShort: info.distance < this.minDistance,
        };
    }

    render() {
        const {children, id, className, setProps} = this.props;
        return (
            <div
                id={id}
                className={className + (this.state.active ? ' active' : '')}
                onTouchStart={(e) => {
                    this.updateActive(true);
                    this.swipeStart(e);
                    this.longTap();
                    setProps({start_timestamp: e.timeStamp});
                }}
                onTouchEnd={(e) => {
                    this.updateActive(false);
                    this.swipeEnd(e);
                    this.longTapEnd();
                    setProps({end_timestamp: e.timeStamp});
                }}
                onTouchMove={(e) => {
                    this.swipeMove(e);
                    setProps({touches: this.touchData(e)});
                }}
                onClick={(e) => setProps({end_timestamp: e.timeStamp})}
                style={{width: '500px', height: '500px', background: 'red'}}
            >
                {children}
            </div>
        );
    }
}

TouchableComponent.defaultProps = {};

TouchableComponent.propTypes = {
    children: PropTypes.node,
    id: PropTypes.string,
    className: PropTypes.string,
    n_clicks: PropTypes.number,
    setProps: PropTypes.func,
};
